<template>
  <div>
    <div class="string-container" :style="stringConatinerStyle" @click="debug">
      <div v-for="(string, index) in tuning" :key="index" :style="{ display: 'flex', flexDirection: 'row', flex: 1 }">
        <div v-for="(noteLocation, index) in noteLocations[tuning.indexOf(string)]" :key="index">
          <div :style="fretStyle(noteLocation)">{{ noteLocation.text }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import Fret from "./Fret";
import noteLocation from "../utils/notePostion";

export default {
  // components: { Fret },
  name: "Fretboard",
  props: {
    screenHeight: String
  },
  data() {
    return {
      height: this.screenHeight,
      numberOfStrings: 6,
      numberOfFrets: 20,
      stringLength: 647.7,
      notes: ["C", "C♯/D♭", "D", "D♯/E♭", "E", "F", "F♯/G♭", "G", "G♯/A♭", "A", "A♯/B♭", "B"],
      colors: ["#7FDBFF", "#39CCCC", "#3D9970", "#2ECC40", "#01FF70", "#FFDC00", "#FF851B", "#FF4136", "#85144b", "#F012BE", "#B10DC9", "#0074D9"],
      tuning: ["E", "A", "D", "G", "B", "E"]
    };
  },
  computed: {
    noteLocationsAmount: function() {
      return this.numberOfStrings * this.numberOfFrets;
    },
    fretDistances() {
      let fretDist = [];
      let newLength = this.stringLength;
      for (let i = 0; i < this.numberOfFrets; i++) {
        let d = newLength / 17.817;
        fretDist.push(d);
        newLength = newLength - d;
      }
      return fretDist;
    },
    fretboardLength() {
      return this.fretDistances.reduce((sum, elem) => sum + elem);
    },
    stringConatinerStyle() {
      return {
        width: this.fretboardLength + "mm"
      };
    },
    noteLocations() {
      let noteLocations = [];
      for (let i = 0; i < this.tuning.length; i++) {
        let firstNoteOfString = this.tuning[i];
        noteLocations.push([]);
        for (let j = 0; j < 20; j++) {
          let noteIndex = (j + this.notes.indexOf(firstNoteOfString)) % 12;
          let note = this.notes[noteIndex];
          let color = this.colors[noteIndex];
          let width = this.fretDistances[j].toString() + "mm";
          noteLocations[i].push(new noteLocation(note, i, j, color, note, width));
        }
      }
      return noteLocations;
    }
  },
  methods: {
    debug() {
      console.log(this.noteLocations);
    },
    fretStyle(noteLocation) {
      return {
        display: "flex",
        flexDirection: "row",
        alignItems: "center",
        justifyContent: "center",
        backgroundColor: noteLocation.color,
        width: noteLocation.width,
        height: "100%"
      };
    }
  }
};
</script>

<style scoped>
.string-container {
  display: flex;
  flex-direction: column;
  /* height: 48.19mm; */
  height: 100vh;
  background-color: red;
}
</style>
